import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    username: []
  },
  getters: {
    users: function (state) {
      return state.username
    }
  },
  mutations: {
    addUser: function (state, payload) {
      state.username.push = payload
    }
  }
})



